.contact {
    background-color: #f7f7ff;
    padding: 50px 0;
}

.item {
    display: flex;
    justify-content: space-between;
}

.formBloc {
    max-width: 500px;
    width: 100%;
}

.form {
    display: flex;
    height: calc(100% - 60px);
    width: 100%;
    flex-direction: column;
    justify-content: center;
}

form input, textarea {
    margin: 10px 0;
}

.btnForm {
    max-width: 120px;
    width: 100%;
    margin: 10px 0;
    padding: 8px;
}

.infoBloc {
    max-width: 370px;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}


.media {
    display: flex;
    align-items: center;
    margin-top: 30px;
}

.mediaBody {
    padding: 0 20px;
}

.icon {
    background: rgba(220, 150, 90, .1);
    padding: 15px;
    border-radius: 50%;
}

@media (max-width: 890px) {
    .item {
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 490px) {
    .mediaBody {
        width: 150px;
        overflow-wrap: break-word;
    }
}